import PropTypes from "prop-types";
import * as Yup from "yup";
import { useEffect, useMemo } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import { Box, Card, Grid, Stack, Typography } from "@mui/material";
// utils
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFSwitch,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import { getTypeTransactions } from "../../../redux/slices/generale";
import {
  addFee,
  updateFee,
  addFeeSuccess,
  updateFeeSuccess,
} from "../../../redux/slices/fee";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

FeeNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentFee: PropTypes.object,
};

export default function FeeNewEditForm({ isEdit, currentFee }) {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const NewFeeSchema = Yup.object().shape({
    type_transaction: Yup.string().required(
      t("field.required", {
        obj: t("field.type_transaction"),
      })
    ),
    minimum_amount: Yup.string().required(
      t("field.required", {
        obj: t("field.minimum_amount"),
      })
    ),
    maximum_amount: Yup.number().required(
      t("field.required", {
        obj: t("field.maximum_amount"),
      })
    ),
    fee_amount: Yup.number().required(
      t("field.required", {
        obj: t("field.fee_amount"),
      })
    ),
  });

  const defaultValues = useMemo(
    () => ({
      type_transaction: currentFee?.type_transaction || "",
      minimum_amount: currentFee?.minimum_amount || 0,
      maximum_amount: currentFee?.maximum_amount || 0,
      fee_amount: currentFee?.fee_amount || 0,
      is_percent: currentFee?.is_percent || true,
      description: currentFee?.description || "",
      keyId: currentFee?.id || -1,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentFee]
  );

  const methods = useForm({
    resolver: yupResolver(NewFeeSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (isEdit && currentFee) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentFee]);

  const onSubmit = async () => {
    try {
      if (isEdit) {
        const results = await updateFee(values);
        dispatch(updateFeeSuccess(results));
      } else {
        const results = await addFee(values);
        dispatch(addFeeSuccess(results));
      }
      reset();
      enqueueSnackbar(!isEdit ? t("success.CREATE") : t("success.UPDATED"));
      navigate(PATH_DASHBOARD.configuration.fee);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
  };

  const { typetransactions } = useSelector((state) => state.generale);

  useEffect(() => {
    dispatch(getTypeTransactions());
  }, [dispatch]);

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFSelect
                name="type_transaction"
                label={t("field.type_transaction")}
                placeholder={t("field.type_transaction")}
                disabled
                value="DEPOSIT"
              >
                <option value="" />
                {typetransactions?.map((option) => (
                  <option key={option} value={option}>
                    {t(`type_transaction.${option}`)}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField
                name="minimum_amount"
                label={t("field.minimum_amount")}
              />
              <RHFTextField
                name="maximum_amount"
                label={t("field.maximum_amount")}
              />
              <RHFTextField name="fee_amount" label={t("field.fee_amount")} />
              <RHFTextField
                name="description"
                multiline
                rows={4}
                label="Description"
              />
              <RHFSwitch
                name="is_percent"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Percent
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{ color: "text.secondary" }}
                    >
                      Enabling this will automatically make the fee amount to
                      percentage
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: "space-between" }}
              />
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {!isEdit
                  ? t("params.create", {
                      obj: t("p_fee.fee"),
                    })
                  : t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
