import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import { getFee, getFees } from "../../redux/slices/fee";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import FeeNewEditForm from "../../sections/@dashboard/configuration/FeeNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function FeeCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { feeKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentFee, setCurrentFee] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
  }, [feeKey]);

  const handleGetObject = async () => {
    try {
      const result = await getFee(Number(feeKey));
      setCurrentFee(result);
      dispatch(getFees());
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.configuration.fee);
    }
  };

  return (
    <Page
      title={`Configuration: ${
        !isEdit ? t("p_fee.new_fee") : t("p_fee.edit_fee")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={!isEdit ? t("p_fee.new_fee") : t("p_fee.edit_fee")}
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("params.configuration"),
              href: PATH_DASHBOARD.configuration.root,
            },
            {
              name: !isEdit
                ? t("p_fee.new_fee")
                : `${t("p_fee.fee")} ${currentFee?.type_transaction_name}`,
            },
          ]}
        />

        <FeeNewEditForm isEdit={isEdit} currentFee={currentFee} />
      </Container>
    </Page>
  );
}
